import React from "react";
import basket from "../images/basket.png";
import heart from "../images/heart.png";
import user from "../images/user.png";
import logo from "../images/Logo_text.png";
import miniLogo from "../images/miniLogo.png";
import reg from "../images/pencil.png";
import LoginModal from "./LoginModal";
import { NavLink } from "react-router-dom";
import { useState } from "react";
import { connect } from "react-redux";
function Header(props) {
  // console.log(props);

  // Resposive mobile bars
  const [open, setOpen] = useState();
  const [search, setSearch] = useState("");
  const [openModal, setOpenModal] = useState(false);

  let [allProducts, setAllProducts] = useState([]);
  let x = allProducts.filter((a) =>
    a.brand.toLowerCase().includes(search.toLowerCase())
  );
  useState(() => {
    allProducts = fetch(
      "https://shahmar700.github.io/shahmar_data_api/shahmar_data.json"
    )
      .then((res) => res.json())
      .then((res) => setAllProducts(res));
  }, []);
  const cleanSearch = () => {
    search.textContent = "";
  };

  return (
    <>
      <header className="header wrapper">
        <i className="fa-solid fa-bars hamburger "></i>
        <NavLink to="/">
          <div className="logoDiv">
            <h1>Metacomp</h1>
            <img src={logo} />
          </div>
        </NavLink>
        <div className="search">
          <div className="formControll">
            <input
              autoComplete="off"
              onChange={(e) => setSearch(e.target.value)}
              value={search}
              type="text"
              placeholder="Axtarış edin"
            />
            <i className="fa-regular fa-magnifying-glass"></i>
            <ul
              data-length={`${x.length} Məhsul tapıldı !`}
              style={{
                display: x.length && search.length >= 2 ? "flex" : "none",
              }}
              className="searchProduct"
            >
              {search.length >= 2 &&
                x.map((a) => (
                  <li key={a.id}>
                    <NavLink onClick={cleanSearch} to={`/description/${a.id}`}>
                      <img src={a.image} alt="image" />
                      <h3>{a.model}</h3>
                    </NavLink>
                  </li>
                ))}
            </ul>
          </div>
        </div>
        <div className="headerRight flex">
          <NavLink to="/basket">
            <div className="sebet">
              <i className="fa-light fa-basket-shopping"></i>
              <p>Səbət</p>
              {props.basket.length ? (
                <span>{props.basket.length}</span>
              ) : (
                props.basket
              )}
            </div>
          </NavLink>
          {/* <img src={heart} /> */}
          {/* Login && SignUp  */}
          {/* <div className="qeydiyyat">
            <NavLink to="/registration">
              <button className="flex align-center">
                <i className="fa-light fa-pen"></i>
                <p>Qeydiyyat</p>
              </button>
            </NavLink>
          </div> */}
          {/* <div className="hesab">
            <button
              className="flex align-center"
              onClick={() => {
                setOpenModal(true);
              }}
            >
              <i className="fa-light fa-user"></i>
              <p>Daxil ol</p>
            </button>
          </div> */}
          {/* Login && SignUp  */}
        </div>
        {openModal && <LoginModal closeModal={setOpenModal} />}
      </header>
    </>
  );
}

let t = (s) => s;
export default connect(t)(Header);
